import Vue from 'vue'
import VueRouter from 'vue-router'
import NavigationBar from '@/views/NavigationBar.vue'
import LoginPage from '@/views/LoginPage.vue'
import Portfolio from '@/views/Portfolio.vue'
import MusicFinder from '@/views/MusicFinder.vue'
import Photography from '@/views/Photography.vue'
import Email from '@/components/Email.vue'
import WelcomeTitle from '@/components/WelcomeTitle.vue'
import Contact from '@/views/Contact.vue'
import SnakeGame from '@/views/SnakeGame.vue'
import WakeApp from '@/views/WakeApp.vue'

Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    component: WelcomeTitle
  },
  {
    path: '*',
    name: 'NavigationBar',
    component: NavigationBar
  },
  {
    path: '/portfolio',
    name: 'Portfolio',
    component: Portfolio
  },
  {
    path: '/musicfinder',
    name: 'MusicFinder',
    component: MusicFinder
  },
  {
    path: '/snakegame',
    name: 'SnakeGame',
    component: SnakeGame
  },
  {
    path: '/photography',
    name: 'Photography',
    component: Photography
  },
  {
    path: '/login',
    name: 'LoginPage',
    component: LoginPage
  },
  {
    path: '/email',
    name: 'Email',
    component: Email
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact
  },
  {
    path: '/wakeapp',
    name: 'WakeApp',
    component: WakeApp
  }
]
const router = new VueRouter({
  mode: 'history',
  routes
})
router.beforeEach((to, from, next) => {
  if (to.matched.some((routes) => routes.meta.requiresAuth)) {
    if (localStorage.getItem('auth') === 'magic@yahoo.se') {
      next()
    } else {
      next({ path: '*' })
    }
  }
  next()
})

export default router
