import Vuex from 'vuex'
import moduleA from './modules/moduleA'
import playerData from './modules/playerData'

export default new Vuex.Store({
  modules: {
    a: moduleA,
    playerData
  },
  state: {
    cards: [
      {
        title: 'Music Finder',
        description:
          'Dynamic PWA application built with Vue CLI. Usage of axios and common data binding features',
        img: '/tictactoe.png',
        routerlink: 'musicfinder'
      },
      {
        title: 'Snake Game',
        description:
          'A traditional game built with Javascript and Vue CLI. Move the snake, collect the apples, and avoid hitting your own tail. Use key codes or controller buttons on screen',
        img: '/snakegame.png',
        routerlink: 'snakegame'
      },
      {
        title: 'Wake app',
        description:
          'Prototype made in Figma using grid system, components and animated transitions',
        img: '/.jpg',
        routerlink: 'wakeapp'
      },
      {
        title: 'Photography',
        description: 'A small collection of photographs taken the last year',
        img: '/.jpg',
        routerlink: 'photography'
      }
    ]
  },
  getters: {
    currentIndex: (state) => {
      return state.currentIndex
    },
    cards: (state) => {
      return state.cards
    }
  },
  mutations: {
    nextIndex: (state) => {
      if (state.currentIndex < state.cards.length - 1) state.currentIndex++
    },
    prevIndex: (state) => {
      if (state.currentIndex > 0) state.currentIndex--
    }
  }
})
