import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default {
  namespaced: true,
  state: {
    userScore: localStorage.getItem('localScore'),
    show: false,
    latestSnakeScore: '',
    snakeHighscore: null
  },

  mutations: {
    setUser(state, payload) {
      localStorage.setItem('Password', payload.Password)
      localStorage.setItem('localScore', '1000')
      localStorage.setItem('SnakeHighscore', '1')
    },
    newSnakeScore(state, x) {
      // Denna funktion tar reda p om den nya pongen frn snake r personligt rekord fr spelaren, isf sparar den det som Highscore. Annars lgger den bara in det som "latestScore" s att anvndaren kan se sin senaste pong i efterhand.
      console.log(x)
      if (x > parseInt(localStorage.getItem('SnakeHighscore'))) {
        localStorage.setItem('SnakeHighscore', x)
        state.snakeHighscore = x
      }
      state.latestSnakeScore = x
    }
  },
  getters: {
    showMe: (state) => state.show,
    snakeHighscore: (state) => state.snakeHighscore,
    latestSnakeScore: (state) => state.latestSnakeScore,
    speed: (state) => Math.ceil(state.score / 2) || 12
  }
}
