<template>
  <div class="online-offline">
    <li class="offline" v-if="off">No network, try to connect</li>
    <li class="online" v-if="on" />
  </div>
</template>
<script>
  export default {
    name: 'OnlineOffline',

    data() {
      return {
        on: true,
        off: false
      }
    },
    mounted() {
      addEventListener('offline', () => {
        this.off = true
        this.on = false
      })
      addEventListener('online', () => {
        this.on = true
        this.off = false
      })
    }
  }
</script>
<style scoped>
  .online-offline {
    display: flex;
    justify-self: end;
    margin-left: 2em;
  }
  li {
    list-style-type: none;
  }
</style>
